#home{
    width: 100%;
    height: 80vh;
    margin-top: 70px;
}
#home h1{
    font-size: 39px;
    font-weight: 700;
    color: #000;
    margin: 0 0 10px 0;

}
.brand_name{
    color: #3498db;
}
#home h2{
    color: #484848;
    font-size: 18px;
}
.home_btn{
    width: 170px;
    color: #3498db;
    font-size: 16px;
    font-weight: 500;
    padding: 10px 30px;
    letter-spacing: 1px;
    border-radius: 50px;
    transition: 0.5s;
    text-decoration: none;
    display: inline-block;
    border: 2px solid #3498db;
}
.home_btn:hover{
    color: #ffff;
    background-color: #3498db;
}
.animated{
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
    0%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}
@keyframes up-down {
    0%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}